import * as actionTypes from './actionTypes';
import { base64DecodeUtf8 } from '@webui-one/core/utils/string';

const initialState = {
};

export default function login(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.LOGIN_PENDING:
        return {
            ...state,
            isLoading: true,
        };
    case actionTypes.LOGIN_SUCCESS:
        return {
            ...state,
            error: undefined,
            isLoading: true,
        };
    case actionTypes.LOGIN_ERROR:
        return {
            ...state,
            error: action.error,
            isLoading: false,
        };
    case actionTypes.LOGIN_CLEAR_ERROR:
        return {
            ...state,
            error: undefined,
            isLoading: false,
        };
    case actionTypes.LOGIN_SESSION_CHECK_START:
        return {
            ...state,
            isCheckingSession: true,
            sessionError: undefined,
            sessionExists: undefined,
            sessionUser: '',
        };
    case actionTypes.LOGIN_SESSION_CHECK_COMPLETE:
        return {
            ...state,
            isCheckingSession: false,
            sessionError: undefined,
            sessionExists: action.sessionStatus.sessionExists,
            sessionUser: action.sessionStatus.userName,
        };
    case actionTypes.LOGIN_SESSION_CHECK_ERROR:
        return {
            ...state,
            isCheckingSession: false,
            sessionError: action.error,
            sessionExists: false,
            sessionUser: '',
        };
    default:
        return state;
    }
}

export const selectError = state => state.login.error;
export const selectChallengeId = (state) => {
    const err = selectError(state);
    return err && err.data && err.data.challenge_id;
};
export const selectChallengePrompt = (state) => {
    const err = selectError(state);
    const challengePrompt = err && err.data && err.data.challenge_prompt;
    if (!challengePrompt) return undefined;
    try {
        return base64DecodeUtf8(challengePrompt);
    } catch (e) {
        return `Failed to base64 decode challenge_prompt: ${challengePrompt}`;
    }
};
export const selectIsLoading = state =>
    state.login && state.login.isLoading === true;
export const selectIsCheckingSession = state =>
    state.login && state.login.isCheckingSession === true;
export const selectSessionExists = state =>
    state.login && state.login.sessionExists;
export const selectSessionUser = state =>
    state.login && state.login.sessionUser;
