// WARNING: This method uses window.location.href which is an attack surface.
// The purpose of the location.href variable is to redirect the user to local or remote pages.
// The problem arrises since this variable can execute malicsious code by
// assigning javscript: or vbscript: strings to the href variable.

// eslint-disable-next-line import/prefer-default-export
export const UNSAFEWindowLocationHref = (href) => {
    if (!/^(\/|https?:)/i.test(href)) {
        throw Error('invalid url');
    } else {
        window.location.href = href;
    }
};
