export const isNativeApplication = (app) => {
    let href;
    if (isObject(app) && isNonEmptyString(app.href)) {
        href = app.href;
    } else if (isNonEmptyString(app)) {
        href = app;
    }

    if (!href) {
        return false;
    }

    return Object.values(defaultApplications).reduce((acc, item) => (
        href === item.href || href.startsWith(`${item.href}/`)
            ? true
            : acc
    ), false);
};

export default defaultApplications;
