
import {
    flagTypes as coreFlagTypes,
    flagIsSet as _flagIsSet,
} from '../flags';
import jsonrpcSimple, { isInvalidSessionError, getTrans as _getTrans } from '../jsonrpc';
import {
    isNetworkError,
    isUnloading,
} from './browser';
import { isDevEnv as _isDevEnv } from './platform';
import {
    isNumber,
} from './number';
import {
    isObject,
} from './object';
import {
    isNonEmptyString,
} from './string';
import {
    gregorianSecondsToUnixTimestamp,
} from './time';
import {
    UNSAFEWindowLocationHref,
} from './unsafe';

export const LOGOUT_REASON = 'logout-reason';
export const CONNECTION_LOST = 'Connection lost';
export const SESSION_INVALID = 'Session no longer valid';
export const SESSION_TERMINATED = 'Session was terminated';
export const TERMINATED_MSG = 'Your session has been terminated by';

let didRedirect = false;

export const hasRedirectedToLogin = () => didRedirect === true;
export const setRedirectedToLogin = (redirected) => {
    didRedirect = redirected;
};
export const skipRedirectToLogin = ({
    funcs: {
        flagIsSet = _flagIsSet,
        isDevEnv = _isDevEnv,
    } = {},
} = {}) => (
    isDevEnv()
    && flagIsSet(coreFlagTypes.SKIP_REDIRECT_TO_LOGIN_ON_FATAL_ERROR, 'true')
);

const SKIP_REDIRECT_TO_LOGIN_MESSAGE = 'Skipping redirect to login on invalid session';
const LOGIN_PATH = '/login.html';

export const redirectToLogin = (oldUrl, {
    hasRedirected = hasRedirectedToLogin,
    logger = console,
    setRedirected = setRedirectedToLogin,
    redirectToUrl = UNSAFEWindowLocationHref,
    skipRedirect = skipRedirectToLogin,
} = {}) => {
    if (hasRedirected()) {
        return;
    }
    setRedirected(true);

    if (skipRedirect()) {
        logger.log(SKIP_REDIRECT_TO_LOGIN_MESSAGE);
        return;
    }

    const redirectUrl = isNonEmptyString(oldUrl)
        ? `${LOGIN_PATH}?oldUrl=${encodeURIComponent(oldUrl)}`
        : LOGIN_PATH;

    redirectToUrl(redirectUrl);
};

const handleInvalidSession = (isAuthenticated) => {
    window.sessionStorage.setItem(LOGOUT_REASON, SESSION_INVALID);
    const oldUrl = isAuthenticated && window.location.pathname;
    redirectToLogin(oldUrl);
};

const handleTerminatedSession = () => {
    window.sessionStorage.setItem(LOGOUT_REASON, SESSION_TERMINATED);
    redirectToLogin(window.location.pathname);
};

let lostConnection = false;
export const setConnectionIsLost = (lost) => { lostConnection = lost; };
export const connectionIsLost = () => lostConnection;

export const handleFatalError = (
    error,
    isAuthenticated,
    {
        setConnectionLost = () => null,
        dispatch = () => null,
        setAuthenticated = () => null,
    } = {},
) => {
    if (isNetworkError(error) && !lostConnection) {
        setConnectionIsLost(true);
        dispatch(setConnectionLost());
        return false;
    }
    if (isInvalidSessionError(error)) {
        if (!isUnloading()) {
            dispatch(setAuthenticated(false));
            handleInvalidSession(isAuthenticated);
        }
        return true;
    }
    if (isObject(error)
        && isNonEmptyString(error.message)
        && error.message.startsWith(TERMINATED_MSG)
    ) {
        dispatch(setAuthenticated(false));
        handleTerminatedSession();
        return true;
    }
    return false;
};

/**
 * Returns an object containing
 * - username
 * - session status
 * - a unix timestamp in milliseconds, indicating when the
 *   session should no longer be valid
 * @returns {Object}
 */
export const hasValidSession = async ({
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    try {
        const {
            ttl: { end_time: endTime } = {},
            user: userName,
        } = await jsonrpc('get_system_setting') || {};
        if (isNonEmptyString(userName) && isNumber(endTime) && endTime >= 0) {
            return {
                sessionEnd: gregorianSecondsToUnixTimestamp(endTime),
                sessionExists: true,
                userName,
            };
        }
        return {
            sessionEnd: -1,
            sessionExists: false,
            userName: '',
        };
    } catch (error) {
        return {
            error,
            sessionEnd: -1,
            sessionExists: false,
            userName: '',
        };
    }
};

export {
    SKIP_REDIRECT_TO_LOGIN_MESSAGE as _SKIP_REDIRECT_TO_LOGIN_MESSAGE,
};

export const extendSession = async ({
    extendIntervalMs = 600000,
    funcs: {
        getTrans = _getTrans,
    } = {},
} = {}) => {
    let running = true;
    let timeoutId;
    const extender = async () => {
        if (running) {
            timeoutId = setTimeout(extender, extendIntervalMs);
            await getTrans();
        }
    };
    await extender();
    return () => {
        clearTimeout(timeoutId);
        running = false;
    };
};
