// NOTE(dbildstr): See doc/flags.md for information on how to use flags
import { isDevEnv } from '@webui-one/core/utils/platform';
import {
    addCallback,
    createUpdater,
    getStore,
    updateFlags,
} from '@webui-one/core/flags';
import * as flagsActionTypes from 'modules/flags/actionTypes';
import flagsReducer from 'modules/flags/reducer';
import { selectFlags } from 'modules/flags/selectors';

let mockFuncs = {
    isDevEnv,
};

const getMockableFunctions = () => mockFuncs;
const setMockableFunctions = (funcs) => { mockFuncs = funcs; };

export const flagMiddleware = store => next => (action) => {
    if (mockFuncs.isDevEnv() && action.type === flagsActionTypes.SET_FLAGS) {
        updateFlags(flagsReducer(selectFlags(store.getState()), action));
    }
    return next(action);
};

export const registerFlags = (p, defProps, createUpdate = createUpdater) => {
    const props = p || {};
    const defaultProps = defProps || {};
    const mod = {};
    const update = createUpdate(mod, props, defaultProps);
    if (mockFuncs.isDevEnv()) {
        addCallback(update);
    }
    update(selectFlags(getStore().getState()));
    return mod;
};

export {
    getMockableFunctions as _getMockableFunctions,
    setMockableFunctions as _setMockableFunctions,
};
