export const selectApplication = state => state.application;
export const selectApplicationName = state => state.application && state.application.name;
export const selectAuthenticated = state => state
    && state.application
    && state.application.authenticated === true;
export const selectHelpIsVisible = state => state.application.helpIsVisible;
export const selectSettingsIsVisible = state => state.application.settingsIsVisible;
export const selectLastLoginDisplayed = state => state.application.lastLoginDisplayed;
export const selectLastLocation = state => state.application.lastLocation;
export const selectUseConfigurationEditorTwo = state =>
    state.application.redirectToConfigurationEditorTwo;
export const selectConnectionLost = state =>
    !!(state.application && state.application.connectionLost);
