export const isString = (value) => {
    if (typeof value === 'string') return true;
    if (typeof value !== 'object') return false;
    return Object.prototype.toString.call(value) === '[object String]';
};

export const isNonEmptyString = str => (
    isString(str) && str !== ''
);

export const truncateLongString = (str, max, min, nrOfVisibleColumns = 1) => {
    if (!isString(str)) throw new TypeError('Input is not a string');
    const truncateLength = max / nrOfVisibleColumns > min ? max / nrOfVisibleColumns : min;
    if (str.length <= truncateLength) return str;

    return `${str.slice(0, truncateLength / 2)}...${str.slice(str.length - truncateLength / 2)}`;
};

export function lstrip(str, format) {
    if (typeof str !== 'string') {
        throw new TypeError('Input is not a string');
    }
    const fmt = typeof format === 'string' && format.length ? format : '\\s';
    return str.replace(new RegExp(`^(${fmt})+`), '');
}

export function rstrip(str, format) {
    if (typeof str !== 'string') {
        throw new TypeError('Input is not a string');
    }
    const fmt = typeof format === 'string' && format.length ? format : '\\s';
    return str.replace(new RegExp(`(${fmt})+$`), '');
}

export function strip(str, format) {
    return rstrip(lstrip(str, format), format);
}

export const pad = (number) => {
    if (number < 10) {
        return `0${number}`;
    }
    return `${number}`;
};

export const parseXML = (xmlString) => {
    const parser = new DOMParser();
    return parser.parseFromString(xmlString, 'application/xml');
};

/**
 * Returns an array containing the left part of the string split by the delimiter,
 * the delimiter itself and the right part of the string.
 * @param {string} delimiter - The delimiter
 * @param {string} string - The string to split
 * @returns {Array}
 */
export const partition = (delimiter, string) => {
    const [head, ...tail] = string.split(delimiter);
    if (tail.length === 0) return [head, '', ''];
    return [head, delimiter, tail.join(delimiter)];
};

/**
 * Returns an array containing the left part of the string split by the delemiter,
 * the delimiter itself and the right part of the string (starting from right).
 * @param {string} delimiter - The delimiter
 * @param {string} string - The string to split
 * @returns {Array}
 */
export const rpartition = (delimiter, string) => {
    const [head, ...tail] = string.split(delimiter).reverse();
    if (tail.length === 0) return ['', '', head];
    return [tail.reverse().join(delimiter), delimiter, head];
};

/**
 * Returns a boolean indicating if the string is valid JSON or not.
 * @param {string} value - The string to check
 * @returns {boolean}
 */
export function isJson(value) {
    try {
        JSON.parse(value);
    } catch (e) {
        return false;
    }
    return true;
}

/**
 * Returns a boolean indicating if the string is valid XML or not.
 * @param {string} value - The string to check
 * @returns {boolean}
 */
export function isXml(value) {
    try {
        const parser = new DOMParser();
        const doc = parser.parseFromString(value, 'application/xml');
        return doc && doc.getElementsByTagName('parsererror').length === 0;
    } catch (e) {
        return false;
    }
}

export function snake2camel(str) {
    if (!isString(str)) {
        throw new TypeError('Input is not a string');
    }
    return str.replace(/_([a-zA-Z])/g, (match, p1) => p1.toUpperCase());
}

export function camel2snake(str) {
    if (!isString(str)) {
        throw new TypeError('Input is not a string');
    }
    return str.replace(/([A-Z])/g, (match, p1) => `_${p1.toLowerCase()}`);
}
// https://stackoverflow.com/a/30106551/4189664
export function base64EncodeUtf8(str) {
    return window.btoa(encodeURIComponent(str).replace(/%([0-9A-F]{2})/g,
        (match, p1) => String.fromCharCode(`0x${p1}`)));
}

const decodeChar = (c) => {
    const hexChar = c.charCodeAt(0).toString(16);
    const ch = `00${hexChar}`.slice(-2);
    return `%${ch}`;
};
// https://stackoverflow.com/a/30106551/4189664
export function base64DecodeUtf8(str) {
    return decodeURIComponent(
        window.atob(str)
            .split('')
            .map(decodeChar)
            .join(''),
    );
}

export const removePrefix = (str, prefix) => (
    str.startsWith(prefix)
        ? str.substr(prefix.length)
        : str
);

export const capitalizeFirstLetter = string => string.charAt(0).toUpperCase() + string.slice(1);
