import { createLogger } from 'redux-logger';

import * as flagTypes from 'modules/flags/flagTypes';

import jsonrpc from '@webui-one/core/jsonrpc';
import { setFlags } from 'modules/flags/actions';
import { flagMiddleware } from 'modules/flags';
import { flagIsSet } from '@webui-one/core/flags';

import { isDevEnv as isDevEnvFunc } from '@webui-one/core/utils/platform';

export const exposeDevCode = (store, { funcs: { isDevEnv = isDevEnvFunc } = {} } = {}) => {
    if (!isDevEnv()) {
        return;
    }

    window._store = store; // eslint-disable-line no-underscore-dangle

    window._getActiveFlags = () => ( // eslint-disable-line no-underscore-dangle
        { ...store.getState().flags }
    );

    window._setFlags = (flags) => { // eslint-disable-line no-underscore-dangle
        store.dispatch(setFlags(flags));
    };

    window._jsonrpc = jsonrpc; // eslint-disable-line no-underscore-dangle
};

export const initDevModeLogger = (
    middlewares,
    composeMethod,
    {
        createReduxLogger = createLogger,
        logger = console, // eslint-disable-line no-console
        funcs: {
            isDevEnv = isDevEnvFunc,
        } = {},
    } = {},
) => {
    if (!isDevEnv()) {
        return [
            middlewares,
            composeMethod,
        ];
    }

    const reduxLogger = createReduxLogger({
        collapsed: true,
        logger,
        predicate: () => !flagIsSet(flagTypes.REDUX_LOG_DISABLE, 'true'),
    });
    return [
        [flagMiddleware, ...middlewares, reduxLogger],
        window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || composeMethod, // eslint-disable-line
    ];
};
