let requestId = 0;
let cookie;

const isNode = isNodeEnv();

export const getNcsAddress = ({
    getEnvFunc = getEnvironment,
    isNodeEnvFunc = isNodeEnv,
} = {}) => {
    if (!isNodeEnvFunc()) {
        return '';
    }
    const env = getEnvFunc();
    return isNonEmptyString(env.NCS_ADDRESS)
        ? env.NCS_ADDRESS
        : 'http://localhost:8080';
};

function JsonRpcError(
    error,
    body,
    { funcs: { isDevMode = isDevEnv } = {} } = {},
) {
    this.message = error.message;
    this.internal = error.internal;
    this.type = error.type;
    this.code = error.code;
    this.data = error.data;
    this.body = body;
    if (isDevMode()) {
        this.stack = (new Error()).stack;
    }
}
JsonRpcError.prototype = Object.create(Error.prototype);

function HttpError(
    method,
    xhr,
    body,
    { funcs: { isDevMode = isDevEnv } = {} } = {},
) {
    this.type = 'http.error';
    this.details = JSON.stringify({ method });
    this.status = xhr.status;
    this.statusText = xhr.statusText;
    this.errorMessage = xhr.responseText;
    this.body = body;
    if (isDevMode()) {
        this.stack = (new Error()).stack;
    }
}
HttpError.prototype = Object.create(Error.prototype);

function getNextRequestId() {
    requestId += 1;
    return requestId;
}

function jsonrpcRequest(method, params, id) {
    return { jsonrpc: '2.0', id, method, params };
}

function newRequest(method, params, id) {
    return JSON.stringify(jsonrpcRequest(method, params, id));
}

const jsonrpcHandlers = new Map();

export function registerJsonRpcHandler(tag, handler) {
    if (!jsonrpcHandlers.has(tag)) {
        jsonrpcHandlers.set(tag, new Set());
    }
    jsonrpcHandlers.get(tag).add(handler);
    return () => {
        jsonrpcHandlers.get(tag).delete(handler);
    };
}

export function getJsonRpcHandlers(tag) {
    if (!jsonrpcHandlers.has(tag)) {
        return [];
    }
    return Array.from(jsonrpcHandlers.get(tag));
}

export function clearJsonRpcHandlers() {
    jsonrpcHandlers.clear();
}

const getCookieFunc = () => cookie;
const getCredentialsFunc = () => CREDENTIALS.SAME_ORIGIN;
const setCookieFunc = (c) => { cookie = c; };

const jsonrpcCommon = (
    fetch,
    body,
    method,
    signal,
    baseUrl,
    {
        getCookie = getCookieFunc,
        getCredentials = getCredentialsFunc,
        setCookie = setCookieFunc,
    } = {}, // options
) => {
    const url = `${baseUrl}/jsonrpc/${method}`;
    const headers = {
        Accept: 'application/json;charset=utf-8',
        'Content-Type': 'application/json;charset=utf-8',
    };

    if (isNode && getCookie()) {
        headers.cookie = getCookie();
    }

    return fetch(url, {
        method: 'POST',
        credentials: getCredentials(),
        headers,
        body,
        signal,
    }).then((response) => {
        if (isNode) {
            const headerCookie = response.headers.get('set-cookie');
            if (headerCookie) {
                setCookie(headerCookie.split(';')[0]);
            }
        }

        if (response.status !== 200) {
            return Promise.reject(new HttpError(method, response, body));
        }

        return response.json();
    }).catch((error0) => {
        const error1 = getJsonRpcHandlers('jsonrpcError')
            .reduce((error, handler) => (
                handler(error, body, method)
            ), error0);
        if (error1 instanceof Error) {
            return Promise.reject(error1);
        }
        return Promise.resolve(error1);
    });
};

export const jsonrpcBatch = ({
    requests,
    signal,
    funcs: {
        fetch = fetchFunc,
        getBaseUrl = getNcsAddress,
        getId = getNextRequestId,
        getCookie = getCookieFunc,
        getCredentials = getCredentialsFunc,
        setCookie = setCookieFunc,
    } = {},
} = {}) => {
    // eslint-disable-next-line no-param-reassign
    const requests0 = Array.isArray(requests) ? requests : [requests];
    const requests1 = getJsonRpcHandlers('jsonrpcBatchRequest')
        .reduce((reqs, handler) => handler(reqs), requests0);
    const requests2 = requests1.filter(r => !(r instanceof Error));

    if (requests2.length === 0) {
        // we either have no requests or all requests triggered an error
        // so we fail here without bothering to call NSO.
        return Promise.reject(requests1);
    }

    const body = requests2.map(({ method, params }) => jsonrpcRequest(method, params, getId()));
    const methods = requests2.map(({ method }) => method);
    const methodsString = uniq(methods).join('-');

    const jsonBody = JSON.stringify(body);
    const baseUrl = getBaseUrl();

    const options = {
        getCookie,
        getCredentials,
        setCookie,
    };

    return jsonrpcCommon(
        fetch,
        jsonBody,
        methodsString,
        signal,
        baseUrl,
        options,
    ).then((result0) => {
        const result1 = getJsonRpcHandlers('jsonrpcBatchResponse')
            .reduce((result, handler) => (
                handler(requests2, result)
            ), result0);
        let resultIndex = 0;
        const results = Array.isArray(result1)
            ? result1
            : new Array(requests2.length).fill(result1);
        return requests1.map((req) => {
            if (req instanceof Error) {
                return req;
            }
            const result = results[resultIndex];
            resultIndex += 1;
            return result;
        });
    }, (err) => {
        const error1 = getJsonRpcHandlers('jsonrpcBatchError')
            .reduce((error, handler) => (
                handler(requests2, error)
            ), err);
        return Promise.reject(error1);
    });
};

export default function jsonrpcSimple(
    method,
    params,
    {
        signal,
        funcs: {
            fetch = fetchFunc,
            getBaseUrl = getNcsAddress,
            getId = getNextRequestId,
            getCookie = getCookieFunc,
            getCredentials = getCredentialsFunc,
            setCookie = setCookieFunc,
        } = {},
    } = {},
) {
    const reqId = getId();
    const baseUrl = getBaseUrl();

    const params1 = getJsonRpcHandlers('jsonrpcSimpleRequest')
        .reduce((par, handler) => (
            handler(method, par)
        ), params);

    if (params1 && params1.error) {
        // we triggered an error in a before handler, so we fail here without
        // bothering to call NSO.
        return Promise.reject(params1.error);
    }

    const body = newRequest(method, params1, reqId);

    const options = {
        getCookie,
        getCredentials,
        setCookie,
    };

    return jsonrpcCommon(
        fetch,
        body,
        method,
        signal,
        baseUrl,
        options,
    ).then((json0) => {
        const json1 = getJsonRpcHandlers('jsonrpcSimpleResponse')
            .reduce((json, handler) => (
                handler(method, params1, json)
            ), json0);

        if (json1.error) {
            return Promise.reject(new JsonRpcError(json1.error, body));
        }
        return json1.result;
    }, (error0) => {
        const error1 = getJsonRpcHandlers('jsonrpcSimpleError')
            .reduce((error, handler) => (
                handler(method, params1, error)
            ), error0);
        return Promise.reject(error1);
    });
}

const findThForPath = (trans, path) => trans.reduce((acc, cur) => {
    const { action_path: actionPath } = cur;
    if (actionPath && path.startsWith(actionPath)) return cur.th;
    return acc;
}, undefined);

const reduceTrans = (transactions, path, db, tag, mode) => (
    transactions.reduce((acc, cur) => {
        if (path) {
            const thForPath = findThForPath(transactions, path);
            if (thForPath !== undefined) return thForPath;
        }
        if (cur.db !== db) return acc;
        if (tag && cur.tag !== tag) return acc;
        if (mode && cur.mode === mode) return cur.th;
        if (!mode && !acc && cur.mode === MODES.READ) return cur.th;
        if (!mode && cur.mode === MODES.READ_WRITE) return cur.th;
        return acc;
    }, undefined)
);

export const findLastTransaction = (transactions, {
    actionPath = undefined,
    path = undefined,
    tag = TAGS.WEBUI_ONE,
    mode = undefined,
    db = DBS.RUNNING,
} = {}) => { // eslint-disable-line sonarjs/cognitive-complexity
    if (!transactions) return undefined;
    if (!Array.isArray(transactions)) return undefined;

    if (actionPath) {
        return Object.values(transactions).reduce((acc, cur) => {
            if (cur.action_path === actionPath && cur.db === DBS.CS_TRANS) {
                return cur.th;
            }
            return acc;
        }, undefined);
    }

    return reduceTrans(transactions, path, db, tag, mode);
};

