export const getSchemas = ({
    includeSubfolders,
    additionalPaths,
    path,
    paths,
    showEmpty,
    th,
    signal,
    funcs: {
        jsonrpc = jsonrpcBatch,
    } = {},
}) => {
    let requests = [];
    if (includeSubfolders && showEmpty === true) {
        requests = [{
            method: 'get_schema',
            params: {
                evaluate_when_entries: true,
                levels: -1,
                path,
                stop_on_list: true,
                th,
            },
        }];
    } else {
        requests = paths.map(p => ({
            method: 'get_schema',
            params: {
                evaluate_when_entries: true,
                levels: 1,
                path: p,
                stop_on_list: false,
                th,
            },
        }));
    }
    if (isNonEmptyArray(additionalPaths)) {
        const additionalRequests = additionalPaths.map(p => ({
            method: 'get_schema',
            params: {
                evaluate_when_entries: true,
                levels: 0,
                path: p,
                stop_on_list: false,
                th,
            },
        }));
        requests = [...requests, ...additionalRequests];
    }
    return jsonrpc({ requests, signal });
};

export const query = ({
    th,
    selection,
    path,
    sort,
    sortOrder,
    initialOffset,
    chunkSize,
    resultAs = 'leaf_value_as_string',
    xpathExpr,
    contextNode,
    signal,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
}) => {
    const params = {
        th,
        path,
        selection,
        result_as: resultAs,
    };

    if (sort !== undefined) params.sort = Array.isArray(sort) ? sort : [sort];
    if (sortOrder !== undefined) params.sort_order = sortOrder;
    if (xpathExpr !== undefined) params.xpath_expr = xpathExpr;
    if (contextNode !== undefined) params.context_node = contextNode;
    if (initialOffset || chunkSize) {
        params.initial_offset = initialOffset || 1;
        params.chunk_size = chunkSize || 10;
    }

    return jsonrpc('query', params, { signal });
};

