export const createDisallowExists = async ({
    parentPath,
    values,
    th,
    signal,
    funcs: {
        createFun = create,
        existsFun = exists,
    } = {},
} = {}) => {
    const validKeyValue = values.map(value => quoteKey(value)).join(' ');
    const path = `${parentPath}{${validKeyValue}}`;
    const { exists: alreadyExists } = await existsFun({ th, path, signal });
    if (alreadyExists) {
        throw Error(`'${validKeyValue}' already exists`);
    }
    await createFun({ th, path, signal });
    return { path };
};

export const createDisallowExistInternal = async ({
    path,
    th,
    signal,
    funcs: {
        createFun = create,
        existsFun = exists,
    } = {},
} = {}) => {
    const { exists: alreadyExists } = await existsFun({ th, path, signal });
    if (alreadyExists) {
        throw Error(`'${path}' already exists`);
    }
    await createFun({ th, path, signal });
    return { path };
};


export const createAllowExists = async ({
    path,
    signal,
    th,
    funcs: {
        createFun = create,
        existsFun = exists,
    } = {},
} = {}) => {
    const { exists: alreadyExists } = await existsFun({ th, path, signal });
    if (!alreadyExists) {
        return createFun({ th, path, signal });
    }
    return Promise.resolve();
};

