import {
    isNonEmptyString,
} from './string';

export const ERROR_TYPES = {
    HANDLED_FATAL_ERROR: 'HandledFatalError',
};

export function isErrorInstance(error, name) {
    if (!(error instanceof Error)) {
        return false;
    }
    if (isNonEmptyString(name)) {
        return error.name === name;
    }
    return true;
}

export function isHandledFatalError(error) {
    if (!error) {
        return false;
    }
    return isErrorInstance(error, ERROR_TYPES.HANDLED_FATAL_ERROR)
        || error.type === ERROR_TYPES.HANDLED_FATAL_ERROR;
}
