import { isFunction } from './functional';
import {
    allowRequests,
    blockRequests,
} from './request';

export const CHROME = 'chrome';
export const EDGE = 'edge';
export const FIREFOX = 'firefox';
export const MSIE11 = 'msie11';
export const MSIE = 'msie';
export const SAFARI = 'safari';

export const MOUSE_BUTTON = {
    PRIMARY: 0, // usually left button
    AUXILIARY: 1, // usually middle button/mouse wheel
    SECONDARY: 2, // usually right button
};

export const detectBrowser = (win = window) => {
    const userAgent = win && win.navigator && win.navigator.userAgent;
    if (!userAgent) return undefined;
    if (userAgent.indexOf('Edge/') > -1) return EDGE;
    if (userAgent.indexOf('Trident/') > -1 &&
        userAgent.indexOf('rv:11') > -1) return MSIE11;

    if (userAgent.indexOf('Trident/') > -1) return MSIE;
    if (userAgent.indexOf('Firefox/') > -1) return FIREFOX;

    const chrome = userAgent.indexOf('Chrome/') > -1;
    const safari = userAgent.indexOf('Safari/') > -1;

    if (chrome && safari) return CHROME;
    if (!chrome && safari) return SAFARI;
    if (chrome) return CHROME;
    return undefined;
};

export const isSafari = () => {
    const browser = detectBrowser();
    return browser === SAFARI;
};

export const isIE11 = () => {
    const browser = detectBrowser();
    return browser === MSIE11 || browser === MSIE;
};

export const isNetworkError = (
    error,
    {
        funcs: {
            getBrowserName = detectBrowser,
        } = {},
    } = {},
) => {
    if (!(error instanceof TypeError)) {
        return false;
    }

    const browser = getBrowserName();
    if (browser === CHROME
        && error.message === 'Failed to fetch'
    ) {
        return true;
    }
    if (browser === FIREFOX
        && error.message === 'NetworkError when attempting to fetch resource.'
    ) {
        return true;
    }
    if (browser === SAFARI
        && error.message.toLowerCase() === 'cancelled') {
        return true;
    }
    if (browser === SAFARI // Safari 15+
        && error.message.toLowerCase().startsWith('load failed')) {
        return true;
    }
    if (error.message === 'Network request failed') {
        return true;
    }
    return false;
};

export function getPathname() {
    return window && window.location ? window.location.pathname : '';
}

export function getRootPath() {
    const pathname = getPathname();
    const dirs = pathname.substr(1).split('/');
    let dirList = dirs.length > 1 ? [''] : [];
    const isCustom = dirs[0] === 'custom';
    const defaultValue = isCustom ? '/custom' : '';
    let found = false;
    if (dirs.length > 1 || isCustom) {
        dirList = dirs.reduce((acc, path) => {
            if (!found) {
                acc.push(path);
            }
            if (path.indexOf('webui') !== -1) {
                found = true;
            }
            return acc;
        }, dirList);
    }
    return found ? dirList.join('/') : defaultValue;
}

let unloading = false;
let blockUnloading = false;
let unloadTimeout = null;
export const getBlockUnloading = () => blockUnloading;
export const setBlockUnloading = (block) => { blockUnloading = block; };
export const resetBlockUnloading = () => { blockUnloading = false; };
export const setUnloading = (value) => { unloading = value; };
export const isUnloading = () => unloading;
export const resetUnloading = () => { unloading = false; };
export const hasUnloadTimeout = () => !!unloadTimeout;
export const clearUnloadTimeout = () => {
    clearTimeout(unloadTimeout);
    unloadTimeout = null;
};
export const setUnloadTimeout = (timeout) => {
    unloadTimeout = timeout;
};

export const onBeforeUnload = (evt) => {
    setUnloading(true);
    if (blockUnloading) {
        evt.preventDefault();
        // set a timeout to restore unloading state if we
        // didn't navigate (it is paused by the
        // browser while showing the confirm dialog)
        setTimeout(() => {
            unloadTimeout = setTimeout(() => {
                setUnloading(false);
            }, 100);
        }, 1);

        // eslint-disable-next-line no-param-reassign
        evt.returnValue = 'Really navigate?';
        return 'Really navigate?';
    }
    return undefined;
};


export const baseHideHandler = () => {
    // Fetch requests might cause issues with the bfCache in some browsers
    // (primarily Chrome), so we block all new requests and terminate any
    // that might still be pending.
    blockRequests(true);
    setUnloading(true);
    clearUnloadTimeout();
};

export const baseShowHandler = (evt) => {
    setUnloading(false);

    // Block fetch requests while we initialize the page.
    blockRequests(false);
    if (evt?.persisted) {
        // FIXME: ENG-29349 - reinitialize the application after navigating
        // back into the webui without reloading the entire page
        setTimeout(() => {
            window.location.reload();
        }, 10);
    } else {
        // We are not running from a persisted page, so it should be
        // safe to allow new requests now.
        setTimeout(() => {
            allowRequests();
        }, 10);
    }
};

let pageHideHandlers = [];
let pageShowHandlers = [];

export const getPageHideHandlers = () => pageHideHandlers;
export const getPageShowHandlers = () => pageShowHandlers;

export const clearPageHideHandlers = () => {
    for (const handler of pageHideHandlers) {
        window.removeEventListener('pagehide', handler, { capture: true });
    }
    pageHideHandlers = [];
};
export const clearPageShowHandlers = () => {
    for (const handler of pageShowHandlers) {
        window.removeEventListener('pageshow', handler, { capture: true });
    }
    pageShowHandlers = [];
};

export const onPageHide = (evt) => {
    for (const handler of pageHideHandlers) {
        handler(evt);
    }
};

export const onPageShow = (evt) => {
    for (const handler of pageShowHandlers) {
        handler(evt);
    }
};

export const registerPageHideHandler = (handler) => {
    if (!isFunction(handler) || pageHideHandlers.includes(handler)) {
        return;
    }

    pageHideHandlers.push(handler);
    if (pageHideHandlers.length === 1) {
        window.addEventListener('pagehide', onPageHide, { capture: true });
    }
};

export const unregisterPageHideHandler = (handler) => {
    if (!pageHideHandlers.includes(handler)) {
        return;
    }

    pageHideHandlers = pageHideHandlers.filter(h => h !== handler);
    if (pageHideHandlers.length === 0) {
        window.removeEventListener('pagehide', onPageHide, { capture: true });
    }
};

export const registerPageShowHandler = (handler) => {
    if (!isFunction(handler) || pageShowHandlers.includes(handler)) {
        return;
    }

    pageShowHandlers.push(handler);
    if (pageShowHandlers.length === 1) {
        window.addEventListener('pageshow', onPageShow, { capture: true });
    }
};

export const unregisterPageShowHandler = (handler) => {
    if (!pageShowHandlers.includes(handler)) {
        return;
    }

    pageShowHandlers = pageShowHandlers.filter(h => h !== handler);
    if (pageShowHandlers.length === 0) {
        window.removeEventListener('pageshow', onPageShow, { capture: true });
    }
};

export const getWindowName = () => window.name;
export const setWindowName = (name) => { window.name = name; };
export const clearWindowName = () => setWindowName('');

export const initializeWindow = () => {
    if (!/^webui-one-\d+$/.test(window.name)) {
        setWindowName(`webui-one-${Date.now()}`);
    }
    window.addEventListener('beforeunload', onBeforeUnload);
    registerPageHideHandler(baseHideHandler);
    registerPageShowHandler(baseShowHandler);
};

export const clearSessionStorage = () => {
    window.sessionStorage.clear();
};

export const outputToFile = (file) => {
    const saveElement = document.createElement('a');
    saveElement.download = file.filename;
    saveElement.href = file.href;
    saveElement.style.display = 'none';
    saveElement.style.height = '0px';
    saveElement.style.width = '0px';
    document.body.appendChild(saveElement);
    saveElement.click();
    document.body.removeChild(saveElement);
};

export const getHistoryState = () => {
    try {
        return window.history.state || {};
    } catch (e) {
        // IE 11 sometimes throws when accessing window.history.state
        // See https://github.com/ReactTraining/history/pull/289
        return {};
    }
};
