import { setStorageTag } from '@webui-one/storage/storage';
import { readLogoutReason } from 'modules/logout/actions';
import { getApplicationStorageTag } from 'modules/application/utils';
import {
    initializeTransactions,
} from 'core/transactionsUtils';
import login from 'modules/login/reducer';
import React from 'react';
import ReactDOM from 'react-dom';
import thunk from 'redux-thunk';
import { exposeDevCode, initDevModeLogger } from 'core/devmode';
import {
    clearWindowName,
} from '@webui-one/core/utils/browser';
import { combineReducers, createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import Login from './LoginContainer';

let mockFuncs = {
    clearWindowName,
    initializeTransactions,
    render: (...renderArgs) => { ReactDOM.render(...renderArgs); },
};

const getMockableFunctions = () => mockFuncs;
const setMockableFunctions = (funcs) => { mockFuncs = funcs; };

export default function renderLogin() {
    mockFuncs.clearWindowName('');

    setStorageTag(getApplicationStorageTag());

    const [
        middlewares,
    ] = initDevModeLogger([thunk]);

    const store = createStore(
        combineReducers({ login }),
        applyMiddleware(...middlewares),
    );

    exposeDevCode(store);

    mockFuncs.initializeTransactions(store);

    store.dispatch(readLogoutReason());

    mockFuncs.render(
        <Provider store={store}>
            <Login />
        </Provider>,
        document.getElementById('root'),
    );
}

export {
    getMockableFunctions as _getMockableFunctions,
    setMockableFunctions as _setMockableFunctions,
};
