import * as actionTypes from './actionTypes';

export const applicationNameSetAction = name => ({
    type: actionTypes.APPLICATION_NAME_SET,
    name,
});
export const setAuthenticated = authenticated => ({
    type: actionTypes.AUTHENTICATED_SET,
    authenticated,
});
export const showHelpAction = value => ({ type: actionTypes.SHOW_HELP, value });
export const showSettingsAction = value => ({ type: actionTypes.SHOW_SETTINGS, value });

export const lastLoginDisplayedAction = () => ({
    type: actionTypes.LAST_LOGIN_DISPLAYED,
    value: true,
});

export const setConfigurationEditorTwoAction = value => ({
    type: actionTypes.SET_CONFIGURATION_EDITOR_TWO,
    value,
});
export const toggleConfigurationEditorTwoAction = () => ({
    type: actionTypes.TOGGLE_CONFIGURATION_EDITOR_TWO,
});
export const resetConfigurationEditorTwoAction = () => ({
    type: actionTypes.RESET_CONFIGURATION_EDITOR_TWO,
});

export const connectionLost = () => ({
    type: actionTypes.CONNECTION_LOST,
});
export const connectionRestored = () => ({
    type: actionTypes.CONNECTION_RESTORED,
});
export const setConnectionLost = () =>
    dispatch => dispatch(connectionLost());

export const setConnectionRestored = () =>
    dispatch => dispatch(connectionRestored());
