import { defaultTo, isFunction } from './functional';
import { camel2snake, snake2camel } from './string';

// const source = {
//     first: 'foo',
//     second: 'bar',
//     third: 'foo',
// };
//
// invert(source);
// => { foo: ['first', 'third'], bar: ['second'] }
export const invert = obj => (
    Object.keys(obj).reduce((acc, key) => (
        { ...acc, [obj[key]]: [...(acc[obj[key]] || []), key] }
    ), {})
);

export const filter = (func, obj) => (
    Object.keys(obj).reduce((acc, cur) => {
        if (func(obj[cur])) {
            return { ...acc, [cur]: obj[cur] };
        }
        return acc;
    }, {})
);

// const source = {
//     first: 'foo',
//     second: 'bar',
//     third: 'foo',
// };
//
// findDuplicateValues(source);
// => { foo: ['first', 'third'] }
export const findDuplicateValues = obj => (
    filter(keys => keys.length > 1, invert(obj))
);


