const getWriteTrans = ({
    db = DBS.RUNNING,
    mode = MODES.READ_WRITE,
    path,
    signal,
    tag: writeTag,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch, getState) => {
        const tag = defaultTo(selectWriteTag(getState()), writeTag);
        const response = await jsonrpc('get_trans', { signal });
        const { trans } = response;
        const th = findLastTransaction(trans, { db, mode, path, tag });

        if (th === undefined) {
            return dispatch(getWriteTransMissingAction());
        }
        const t = Object.values(trans).find(tr => tr.th === th);
        return dispatch(getWriteTransAction({
            actionPath: t.action_path,
            db: t.db,
            mode: t.mode,
            tag: t.tag,
            th: t.th,
        }));
    }
);

const getTransChanges = ({
    signal,
    th,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        try {
            const response = await jsonrpc(
                'get_trans_changes',
                {
                    output: 'compact',
                    signal,
                    th,
                },
            );
            const changes = response.changes.map(change => ({
                ...change,
                path: pathToObjects(change.path, { quoteChars: ['"'] }).pop().path,
            }));
            return dispatch(getTransChangesAction(th, changes));
        } catch (error) {
            return dispatch(getTransChangesErrorAction(th, error));
        }
    }
);

export const NEW_TRANS_RETRY = 100;
export const UPGRADE_MODE_ERROR = 'node is in upgrade mode';

const newTransInternal = ({
    actionPath,
    db,
    mode,
    signal,
    tag: newTag,
    th,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (dispatch, getState) => {
    const tag = mode === MODES.READ_WRITE
        ? defaultTo(selectWriteTag(getState()), newTag)
        : defaultTo(selectReadTag(getState()), newTag);

    const params = { db, mode, conf_mode: 'private', tag };

    if (actionPath !== undefined) {
        params.action_path = actionPath;
    }
    if (th) {
        params.th = th;
    }

    return jsonrpc('new_trans', params, { signal })
        .then(res => dispatch(newTransactionAction(res.th)))
        .catch((err) => {
            if (err.data.reason === UPGRADE_MODE_ERROR) {
                return mockFuncs.waiter(() => dispatch(newTransInternal({
                    actionPath,
                    db,
                    mode,
                    signal,
                    tag,
                    th,
                    funcs: { jsonrpc },
                })));
            }
            return dispatch(newTransactionErrorAction(err));
        });
};

const newTrans = ({
    actionPath,
    db,
    mode,
    signal,
    tag,
    th,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => async (dispatch, getState) => {
    if (selectUpgradePending(getState())) {
        return mockFuncs.waiter(() => dispatch(
            newTrans({ actionPath, db, mode, signal, tag, th, funcs: { jsonrpc } }),
        ), NEW_TRANS_RETRY);
    }

    return dispatch(newTransInternal({
        actionPath,
        db,
        mode,
        signal,
        tag,
        th,
        funcs: { jsonrpc },
    }));
};

const tryGetTransChanges = ({
    path,
    signal,
    funcs: {
        getTransChangesFunc = getTransChanges,
        getWriteTransFunc = getWriteTrans,
    } = {},
} = {}) =>
    dispatch =>
        dispatch(getWriteTransFunc({ path, signal })).then((res) => {
            const { th } = res;

            if (th) {
                return dispatch(getTransChangesFunc({ th, signal }));
            }

            return dispatch(
                getTransChangesErrorAction(
                    null,
                    new Error('No valid write transaction found'),
                ),
            );
        });

const deleteTrans = (
    th,
    {
        signal,
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    (dispatch, getState) => {
        const state = getState();
        const changes = selectChanges(state);
        const dirtyPaths = selectDirtyPaths(state);
        const status = selectTransactionDeleteStatus(state, th);
        if (status === deleteStatus.DELETING) {
            return Promise.resolve({});
        }
        dispatch(deleteTransStartAction(th));
        return jsonrpc('delete_trans', { th, signal })
            .then(() => dispatch(deleteTransAction(th, changes, dirtyPaths)))
            .catch(error => dispatch(deleteTransErrorAction(th, error)));
    }
);

function read({
    actionPath,
    path,
    db = DBS.RUNNING,
    tag,
    mode = MODES.READ_WRITE,
    forceRead = false,
    signal,
    funcs: {
        deleteTransThunk = deleteTrans,
        getTransFunc = getTrans,
        newTransFunc = newTrans,
    } = {},
} = {}) {
    return (dispatch, getState) => {
        const state = getState();
        const pendingRead = selectPendingRead(state);
        const upgradePending = selectUpgradePending(state);
        const commitPending = selectCommitInProgress(state);
        const readTag = defaultTo(selectReadTag(state), tag);
        const writeTag = defaultTo(selectWriteTag(state), tag);
        const thRead = selectReadTh(state, { db, tag: readTag });
        const thWrite = selectReadWriteTh(
            state,
            { actionPath, db, tag: writeTag, path },
        );

        if (upgradePending || pendingRead || commitPending) {
            return mockFuncs.waiter(() => dispatch(read({
                actionPath,
                path,
                db,
                tag,
                mode,
                signal,
            })), 100);
        }

        if (thWrite && forceRead) {
            return dispatch(deleteTransThunk(thWrite)).then(() => (
                mockFuncs.waiter(() => dispatch(read({
                    actionPath,
                    path,
                    db,
                    tag,
                    mode: MODES.READ,
                    signal,
                })), 100)
            ));
        }

        if (thWrite && mode === MODES.READ_WRITE) {
            return Promise.resolve(readAction({
                th: thWrite,
                mode: MODES.READ_WRITE,
                tag: writeTag,
                db,
            }));
        }

        if (thRead) {
            return Promise.resolve(readAction({
                th: thRead,
                tag: readTag,
                db,
            }));
        }

        dispatch(readPendingAction(true));

        return dispatch(getTransFunc({ signal })).then((res) => {
            const serverThWrite = findLastTransaction(
                res.transactions,
                {
                    actionPath,
                    db,
                    mode: MODES.READ_WRITE,
                    path,
                    tag,
                },
            );
            const serverThRead = findLastTransaction(
                res.transactions,
                { mode: MODES.READ, db, tag: readTag },
            );

            if (serverThWrite && mode === MODES.READ_WRITE) {
                return dispatch(readAction({
                    th: serverThWrite,
                    mode: MODES.READ_WRITE,
                    tag: writeTag,
                    db,
                }));
            }

            if (serverThRead) {
                return dispatch(readAction({
                    th: serverThRead,
                    tag: readTag,
                    db,
                }));
            }

            return dispatch(newTransFunc({
                db,
                tag: readTag,
                mode: MODES.READ,
                signal,
            })).then((newTransRes) => {
                if (newTransRes.error) {
                    dispatch(readErrorAction(newTransRes.error));
                    return Promise.reject(newTransRes.error);
                }
                return dispatch(readAction({
                    th: newTransRes.th,
                    tag: readTag,
                    db,
                }));
            });
        });
    };
}

// This creates a double-trans-in-trans action transaction for actions that
// adds changes to a running read-write configure transaction.
//
// For performance reasons this is currently limited to a few specific actions
// (see 'actionsWithMainParentTransaction' in the 'constants' module), but in
// the future we might want to try to auto-detect if an action supports this
// by checking if the 'tailf:cli-configure-mode' flag exists on the action
// before creating the action transaction.
const maybeCreateParentTransaction = ({
    actionPath,
    db,
    newTransFunc,
    signal,
    state,
    tag,
}) => (dispatch) => {
    let promise = Promise.resolve({ });
    if (!actionPath) {
        return promise;
    }
    if (!actionsWithMainParentTransaction.some(r => r.test(actionPath))) {
        return promise;
    }
    const maybeMainTh = selectReadWriteTh(
        state,
        { db: DBS.RUNNING, tag: TAGS.WEBUI_ONE },
    );
    if (maybeMainTh) {
        promise = Promise.resolve({ th: maybeMainTh });
    } else {
        promise = dispatch(newTransFunc({
            db,
            mode: MODES.READ_WRITE,
            signal,
            tag: TAGS.WEBUI_ONE,
        }));
    }
    return promise.then(({ th: mainTh }) => dispatch(newTransFunc(({
        db: DBS.RUNNING,
        mode: MODES.READ_WRITE,
        signal,
        tag: `${tag}-action-param-trans`,
        th: mainTh,
    }))));
};

function write({
    actionPath,
    path,
    db = DBS.RUNNING,
    tag,
    signal,
    funcs: {
        getTransFunc = getTrans,
        newTransFunc = newTrans,
    } = {},
} = {}) {
    return (dispatch, getState) => {
        const state = getState();
        const writePending = selectWritePending(state);
        const upgradePending = selectUpgradePending(state);
        const commitPending = selectCommitInProgress(state);
        const writeTag = defaultTo(selectWriteTag(state), tag);
        const thWrite = selectReadWriteTh(
            state,
            { actionPath, db, path, tag: writeTag },
        );

        if (upgradePending || writePending || commitPending) {
            return mockFuncs.waiter(() => dispatch(write({
                actionPath,
                db,
                tag,
                signal,
            })), 100);
        }

        if (thWrite) {
            return Promise.resolve(writeAction({
                actionPath,
                th: thWrite,
                tag: writeTag,
                db,
            }));
        }

        dispatch(writePendingAction(true));

        return dispatch(getTransFunc({ signal })).then((res) => {
            const mode = actionPath ? MODES.READ : MODES.READ_WRITE;
            const serverThWrite = findLastTransaction(
                res.transactions,
                {
                    actionPath,
                    db,
                    mode,
                    path,
                    tag: writeTag,
                },
            );

            if (serverThWrite) {
                return dispatch(writeAction({
                    actionPath,
                    th: serverThWrite,
                    tag: writeTag,
                    db,
                }));
            }

            return dispatch(maybeCreateParentTransaction({
                actionPath,
                db,
                newTransFunc,
                signal,
                state,
                tag: writeTag,
            })).then(({ th: parentTh }) => dispatch(newTransFunc({
                actionPath,
                db,
                mode,
                signal,
                tag: writeTag,
                th: parentTh,
            }))).then((newTransRes) => {
                if (newTransRes.error) {
                    dispatch(writeErrorAction(newTransRes.error));
                    return Promise.reject(newTransRes.error);
                }
                return dispatch(writeAction({
                    actionPath,
                    th: newTransRes.th,
                    tag: writeTag,
                    db,
                }));
            });
        }, error => Promise.reject(dispatch(writeErrorAction(error))));
    };
}

const commitDryRun = ({
    th,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        const flags = ['dry-run=cli'];
        try {
            dispatch(commitDryRunStartAction(th));
            await jsonrpc('validate_commit', { flags, th });
            const response = await jsonrpc('commit', { flags, th });
            return dispatch(commitDryRunCompleteAction(th, response));
        } catch (error) {
            return dispatch(commitDryRunErrorAction(th, error));
        }
    }
);

