const tryToResolveConflicts = ({
    th,
    error,
    funcs: {
        jsonRpcFunc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        if (error && error.message === 'A conflict must be resolved') {
            const conflicts = await jsonRpcFunc('get_trans_conflicts', { th });
            dispatch(transConflictsAction(th, conflicts.conflicts));
        }
    }
);

const validateTrans = (
    th,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    async (dispatch) => {
        try {
            const response = await jsonrpc('validate_trans', { th });
            return dispatch(validateTransAction(th, response));
        } catch (error) {
            await dispatch(tryToResolveConflicts({ th, error }));
            return dispatch(validateTransErrorAction(th, error));
        }
    }
);

