import {
    registerJsonRpcHandler,
} from '@webui-one/core/jsonrpc';
import { ERROR_TYPES } from '@webui-one/core/utils/error';
import {
    setConnectionLost,
    setConnectionRestored,
    setAuthenticated,
} from 'modules/application/actions';
import {
    selectAuthenticated,
    selectConnectionLost,
} from 'modules/application/selectors';
import {
    connectionIsLost,
    handleFatalError as handleFatalErrorFunc,
    setConnectionIsLost,
} from '@webui-one/core/utils/session';
import {
    deleteTransAsyncAction,
    transactionEndAction,
    transactionStartAction,
    transactionsEndAction,
    transactionsStartAction,
} from '@webui-one/components/contexts/transactions/actions';
import {
    selectTransactionDeleteStatus,
    selectTransactionRefCount,
} from '@webui-one/components/contexts/transactions/selectors';
import { deleteStatus } from '@webui-one/components/contexts/transactions/constants';

export function restoreConnection(connectionLost, value, dispatch) {
    if (!connectionLost || value instanceof Error || value.error) {
        return;
    }

    setConnectionIsLost(false);
    dispatch(setConnectionRestored());
}

export const jsonrpcSimpleRequest = (dispatch, getState) => (method, params) => {
    if (params && params.th && method !== 'delete_trans') {
        const { th } = params;
        const status = selectTransactionDeleteStatus(getState(), th);
        if (status === deleteStatus.DELETE_PENDING
            || status === deleteStatus.DELETING
        ) {
            return new Error('Transaction is no longer valid');
        }
        dispatch(transactionStartAction(th));
    }
    return params;
};

export const jsonrpcSimpleResponse = (dispatch, getState) =>
    (method, params, result) => {
        if (method !== 'delete_trans' && params && params.th) {
            const { th } = params;
            dispatch(transactionEndAction(th));
            const state = getState();
            const status = selectTransactionDeleteStatus(state, th);
            const refCount = selectTransactionRefCount(state, th);
            if (status === deleteStatus.DELETE_PENDING && refCount === 0) {
                dispatch(deleteTransAsyncAction(th));
            }
        }
        restoreConnection(selectConnectionLost(getState()), result, dispatch);
        return result;
    };

export const jsonrpcBatchRequest = (dispatch, getState) =>
    (requests) => {
        const thRefCounts = new Map();
        const nextRequests = requests.reduce((reqs, req) => {
            const { method, params = {} } = req;
            if (params.th && method !== 'delete_trans') {
                const { th } = params;
                const status = selectTransactionDeleteStatus(getState(), th);
                if (status === deleteStatus.DELETE_PENDING
                    || status === deleteStatus.DELETING
                ) {
                    reqs.push(new Error('Transaction is no longer valid'));
                    return reqs;
                }
                const thRefCount = thRefCounts.get(th) || 0;
                thRefCounts.set(th, thRefCount + 1);
            }
            reqs.push(req);
            return reqs;
        }, []);
        if (thRefCounts.size > 0) {
            const refCounts = Array.from(thRefCounts);
            dispatch(transactionsStartAction(refCounts));
        }
        return nextRequests;
    };

export const jsonrpcBatchResponse = (dispatch, getState) =>
    (requests, result) => {
        const thRefCounts = new Map();
        requests.forEach((req) => {
            const { method, params = {} } = req;
            if (params.th && method !== 'delete_trans') {
                const { th } = params;
                const thRefCount = thRefCounts.get(th) || 0;
                thRefCounts.set(th, thRefCount + 1);
            }
        });
        if (thRefCounts.size > 0) {
            const refCounts = Array.from(thRefCounts);
            dispatch(transactionsEndAction(refCounts));
            refCounts.forEach(([th]) => {
                const state = getState();
                const status = selectTransactionDeleteStatus(state, th);
                const thRefCount = selectTransactionRefCount(state, th);
                if (status === deleteStatus.DELETE_PENDING && thRefCount === 0) {
                    dispatch(deleteTransAsyncAction(th));
                }
            });
        }
        restoreConnection(selectConnectionLost(getState()), result, dispatch);
        return result;
    };

export const jsonrpcError = (
    dispatch,
    getState,
    {
        funcs: {
            connectionLost = connectionIsLost,
            handleFatalError = handleFatalErrorFunc,
        } = {},
    } = {},
) => (error) => {
    const fatal = handleFatalError(
        error,
        selectAuthenticated(getState()),
        {
            setConnectionLost,
            dispatch,
            setAuthenticated,
        },
    );
    // Ignore errors when connection is down
    if (connectionLost() || fatal) {
        // NOTE(dbildstr): return a json error structure instead of
        // the error if it was fatal to suppress unhandled error warnings
        return {
            error: {
                message: 'Handled fatal error',
                type: ERROR_TYPES.HANDLED_FATAL_ERROR,
            },
        };
    }
    return error;
};

export const initializeTransactions = ({ dispatch, getState }) => {
    const unregisterJsonRpcError = registerJsonRpcHandler(
        'jsonrpcError',
        jsonrpcError(dispatch, getState),
    );
    const unregisterBeforeSimple = registerJsonRpcHandler(
        'jsonrpcSimpleRequest',
        jsonrpcSimpleRequest(dispatch, getState),
    );
    const unregisterAfterSimpleError = registerJsonRpcHandler(
        'jsonrpcSimpleError',
        jsonrpcSimpleResponse(dispatch, getState),
    );
    const unregisterAfterSimpleResult = registerJsonRpcHandler(
        'jsonrpcSimpleResponse',
        jsonrpcSimpleResponse(dispatch, getState),
    );
    const unregisterBeforeBatch = registerJsonRpcHandler(
        'jsonrpcBatchRequest',
        jsonrpcBatchRequest(dispatch, getState),
    );
    const unregisterAfterBatchError = registerJsonRpcHandler(
        'jsonrpcBatchError',
        jsonrpcBatchResponse(dispatch, getState),
    );
    const unregisterAfterBatchResult = registerJsonRpcHandler(
        'jsonrpcBatchResponse',
        jsonrpcBatchResponse(dispatch, getState),
    );
    return () => {
        unregisterJsonRpcError();
        unregisterBeforeSimple();
        unregisterAfterSimpleError();
        unregisterAfterSimpleResult();
        unregisterBeforeBatch();
        unregisterAfterBatchError();
        unregisterAfterBatchResult();
    };
};
