import {
    ensureArray,
    isNonEmptyArray,
} from '@webui-one/core/utils/array';
import { defaultTo } from '@webui-one/core/utils/functional';
import {
    isPositiveNumber,
} from '@webui-one/core/utils/number';

import {
    DBS,
    MODES,
    TAGS,
} from '@webui-one/core/jsonrpc/constants';

import {
    transactionStatus,
    validationStatus,
} from './constants';

const EMPTY_OBJECT = Object.freeze({});
export const getTransactions = state => (
    state?.transactions || state || EMPTY_OBJECT
);

export const selectTags = state => getTransactions(state).tags || EMPTY_OBJECT;
export const selectReadTag = state => selectTags(state).read || TAGS.WEBUI_ONE;
export const selectWriteTag = state => selectTags(state).readWrite || TAGS.WEBUI_ONE;

const getTrans = state => getTransactions(state).trans || {};

export const selectWriteTrans = state =>
    Object.values(getTrans(state))
        .filter(obj => obj.mode === MODES.READ_WRITE && obj.db === DBS.RUNNING);

const pathMatches = (path, matchingPath) => (
    path
    && (matchingPath === path
        || path.startsWith(`${matchingPath}/`)
    )
);

export const selectTrans = (
    state,
    {
        actionPath = undefined,
        db = DBS.RUNNING,
        mode,
        tag: selectedTag,
        path,
    } = {},
) => {
    const trans = getTrans(state);

    const tag = mode === MODES.READ_WRITE
        ? defaultTo(selectWriteTag(state), selectedTag)
        : defaultTo(selectReadTag(state), selectedTag);

    if (actionPath) {
        return Object.values(trans).find(t => (
            pathMatches(actionPath, t.actionPath)
            && t.db === DBS.CS_TRANS
        ));
    }

    return Object.keys(trans).reduce((acc, key) => {
        const val = trans[key];

        if (pathMatches(path, val.actionPath)) {
            return val;
        }
        if (acc === undefined && val.db === db && val.tag === tag && val.mode === mode) {
            return val;
        }
        return acc;
    }, undefined);
};

const selectReadTrans = (state, args) => (
    selectTrans(state, { ...args, mode: MODES.READ })
);

export const selectTransactionByTh = (state, th) => (
    Object.values(getTrans(state)).find(t => t.th === th) || {}
);

export const selectReadWriteTrans = (state, args) => {
    const activeTh = getTransactions(state).activeTh;
    if (activeTh) return selectTransactionByTh(state, activeTh);
    return selectTrans(state, { ...args, mode: MODES.READ_WRITE }) || {};
};

export const selectReevaluateRunning = (state, args) => (
    selectReadWriteTrans(state, args).reEvaluateRunning
);

export const selectActiveTh = state => getTransactions(state).activeTh;
export const selectReadWriteThTag = state => (selectReadWriteTrans(state) || {}).tag;

export const selectActionPath = (state, actionPath) => {
    const actionTrans = selectTrans(state, { actionPath });
    return actionTrans && actionTrans.actionPath;
};

export const selectReadTh = (state, args) => (selectReadTrans(state, args) || {}).th;
export const selectReadWriteTh = (state, args) => (selectReadWriteTrans(state, args) || {}).th;


export const selectChanges = (state, args) => selectReadWriteTrans(state, args).changes;
export const selectNumberOfChanges = state => (selectChanges(state) || []).length;
export const selectCommitInProgress = state => selectReadWriteTrans(state).commitInProgress;
export const selectCommitStartTime = state => selectReadWriteTrans(state).startedAt;
export const selectCommitDryRunInProgress = state => (
    selectReadWriteTrans(state).commitDryRunInProgress
);
export const selectCommitDryRunResult = state => selectReadWriteTrans(state).commitDryRunResult;
export const selectCommitDryRunError = state => selectReadWriteTrans(state).commitDryRunError;
export const selectError = state => selectReadWriteTrans(state).error;
export const selectCommitFailTime = state => selectReadWriteTrans(state).failedAt;
export const selectHasLatestCommitDryRunResult = (state) => {
    const { hasLatestCommitDryRunResult, commitDryRunResult } = selectReadWriteTrans(state);
    if (commitDryRunResult && Object.values(commitDryRunResult).length > 0) {
        return hasLatestCommitDryRunResult;
    }
    return false;
};
export const selectHasLatestNativeCommitDryRunResult = (state) => {
    const {
        hasLatestNativeCommitDryRunResult,
        nativeCommitDryRunResult,
    } = selectReadWriteTrans(state);
    if (nativeCommitDryRunResult) {
        return hasLatestNativeCommitDryRunResult;
    }
    return false;
};


export const selectNativeCommitDryRunInProgress = state =>
    selectReadWriteTrans(state).nativeCommitDryRunInProgress;
export const selectNativeCommitDryRunResult = state =>
    selectReadWriteTrans(state).nativeCommitDryRunResult;
export const selectNativeCommitDryRunError = state =>
    selectReadWriteTrans(state).nativeCommitDryRunError;
export const selectRead = state => selectReadWriteTh(state);
export const selectReadWrite = state => selectReadWriteTh(state);
export const selectReadWriteOrRead = state => selectReadWriteTh(state) || selectReadTh(state);
export const selectResolveInProgress = state => (
    Object.values(getTrans(state)).reduce((acc, trans) => {
        if (trans && trans.thIsBusy) return true;
        return acc;
    }, false)
);

export const selectStatus = state => selectReadWriteTrans(state).status;
export const selectValid = state => selectStatus(state) !== transactionStatus.INVALID;
export const selectValidateTransactionError = state => (
    selectReadWriteTrans(state).validateTransError
);
export const selectHasValidateTransactionError = (state) => {
    const transError = selectValidateTransactionError(state);
    return transError
        && transError.data
        && isNonEmptyArray(transError.data.errors);
};

export const selectWarnings = state => selectReadWriteTrans(state).warnings;
export const selectCommitOptions = state => getTransactions(state).commitOptions;
export const selectRollbacks = state => getTransactions(state).rollbacks;
export const selectUpgradePending = state => getTransactions(state).upgradePending;
export const selectLoadInProgress = state => getTransactions(state).loadInProgress;
export const selectLoadFromFileError = state => getTransactions(state).loadFromFileError;
export const selectLoadFromTextError = state => getTransactions(state).loadFromTextError;
export const selectSaveToFileError = state => getTransactions(state).saveToFileError;

export const selectValidationStatus = (state) => {
    if (selectHasValidateTransactionError(state)) {
        return validationStatus.ERRORS;
    }
    if (isNonEmptyArray(selectWarnings(state))) {
        return validationStatus.WARNINGS;
    }
    return validationStatus.OK;
};

export const selectConflicts = state => (
    selectReadWriteTrans(state).conflicts
);

export const transactionIsEmpty = state => !isNonEmptyArray(selectChanges(state));

export const selectDirtyPaths = (state, args) => (
    ensureArray(selectChanges(state, args))
);

export const pathIsDeleted = (path, dirtyPaths) => ensureArray(dirtyPaths)
    .some(dp => (
        (
            dp.path === path
            || path.startsWith(`${dp.path}/`)
        )
        && dp.op === 'deleted'
    ));

export const pathIsDirty = (path, dirtyPaths) => ensureArray(dirtyPaths)
    .some(dp => (
        dp.path === path
        || dp.path.startsWith(`${path}/`)
        || dp.path.startsWith(`${path}{`)
    ));

export const selectTransactionDeleteStatus = (state, th) =>
    selectTransactionByTh(state, th).deleteStatus;
export const selectTransactionRefCount = (state, th) => {
    const refCount = selectTransactionByTh(state, th).refCount;
    return isPositiveNumber(refCount) ? refCount : 0;
};

export const selectPendingRead = state => getTransactions(state).pendingRead;
export const selectWritePending = state => getTransactions(state).writePending;
export const selectLastCommit = state => getTransactions(state).lastCommit || {};
export const selectRollbackId = state => selectLastCommit(state).rollbackId;
export const selectCommitCompleteTime = state => selectLastCommit(state).completedAt;
export const selectCommitQueueId = state => selectLastCommit(state).commitQueueId;
export const selectCommitQueueInfo = state => selectLastCommit(state).commitQueueInfo;
export const selectCommitQueueStatus = state => selectLastCommit(state).commitQueueStatus;
