/* eslint-disable import/prefer-default-export */

export const ensureNumber = num => (
    Number.isFinite(num) ? num : 0
);

export const isNumber = value => Number.isFinite(value);
export const isPositiveNumber = value => isNumber(value) && value > 0;

export const toNumber = n => ~~n; // eslint-disable-line no-bitwise

// note: trivial, only consider numbers
const parseVersion = str => str.split('.').map(s => parseInt(s, 10));
export const compareVersion = (vA, vB, length = 2) => {
    const a = parseVersion(vA);
    const b = parseVersion(vB);
    for (let i = 0; i < length; i += 1) {
        const ai = a[i] || 0;
        const bi = b[i] || 0;
        if (ai < bi) return 1;
        if (ai > bi) return -1;
    }
    return 0;
};

export function formatBytes(bytes, decimals = 2) {
    if (!+bytes) return '0 Bytes';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return `${parseFloat((bytes / k ** i).toFixed(dm))} ${sizes[i]}`;
}
