import { hasProp } from '../utils/object';
import { isDevEnv } from '../utils/platform';

let mockFuncs = {
    isDevEnv,
};

const getMockableFunctions = () => mockFuncs;
const setMockableFunctions = (funcs) => { mockFuncs = funcs; };

let flagStore;
const EmptyObject = Object.freeze({});
const callbacks = [];

const updateFlags = (state) => {
    callbacks.forEach((cb) => {
        cb(state);
    });
};
const resetCallbacks = () => { callbacks.splice(0); };
const addCallback = (cb) => { callbacks.push(cb); };
const getCallbacks = () => callbacks;

const findMatchInState = (flags, props) =>
    Object.keys(flags).reduce((acc, key) => {
        if (acc) {
            return acc;
        }
        const prop = props[key] && props[key][flags[key]];
        return prop || acc;
    }, null);

const replaceProps = (orgProps, newProps) => {
    // NOTE(dbildstr): it is OK for orgProps to be modified here, since we are
    // changing exported properties of a loaded module on the fly
    const props = Object.keys(newProps).reduce((acc, name) => {
        acc[name] = newProps[name];
        return acc;
    }, orgProps);
    return Object.keys(orgProps).reduce((acc, name) => {
        if (!hasProp(newProps, name)) {
            delete acc[name];
        }
        return acc;
    }, props);
};
const createUpdater = (mod, props, defaultProps) => flagsState => (
    replaceProps(mod, findMatchInState(flagsState, props) || defaultProps)
);

const getStore = () => {
    if (mockFuncs.isDevEnv() && flagStore) {
        return flagStore;
    }
    return { getState: () => EmptyObject };
};

const setStore = (store) => { flagStore = store; };

const flagIsSet = (flag, value = true) => {
    const { flags } = getStore().getState();
    return mockFuncs.isDevEnv() &&
        hasProp(flags, flag) &&
        value === flags[flag];
};

export const flagWrapper = (
    matches,
    defaultFunc,
    {
        funcs: {
            flagIsSetFunc = flagIsSet,
        } = {},
    } = {},
) => (...args) => {
    const func = matches.reduce((acc, [flag, value, f]) => {
        if (flagIsSetFunc(flag, value)) {
            return f;
        }
        return acc;
    }, null) || defaultFunc;
    return func(...args);
};

export {
    addCallback,
    createUpdater,
    getStore,
    findMatchInState,
    flagIsSet,
    replaceProps,
    resetCallbacks,
    setStore,
    updateFlags,
    getCallbacks as _getCallbacks,
    getMockableFunctions as _getMockableFunctions,
    setMockableFunctions as _setMockableFunctions,
};
