import { connect } from 'react-redux';
import { checkSession, login, loginClearErrorAction, challenge, redirectToWebui } from 'modules/login/actions';
import {
    selectChallengeId,
    selectChallengePrompt,
    selectError,
    selectIsCheckingSession,
    selectIsLoading,
    selectSessionExists,
    selectSessionUser,
} from 'modules/login/reducer';
import { clearSessionData } from 'modules/logout/actions';
import Login from './Login';


const mapDispatchToProps = dispatch => ({
    onCheckSession() {
        dispatch(checkSession());
    },
    onLogin(user, passwd) {
        dispatch(login(user, passwd));
    },
    onChallenge(user, passwd) {
        dispatch(challenge(user, passwd));
    },
    onClearError() {
        dispatch(loginClearErrorAction());
    },
});

const mapStateToProps = state => ({
    challengeId: selectChallengeId(state),
    challengePrompt: selectChallengePrompt(state),
    clearSessionData,
    error: selectError(state),
    isCheckingSession: selectIsCheckingSession(state),
    isLoading: selectIsLoading(state),
    redirectToWebui,
    sessionExists: selectSessionExists(state),
    username: selectSessionUser(state),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(Login);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
