import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import { isPositiveNumber, toNumber } from '@webui-one/core/utils/number';
import '@webui-one/styles/styleSheetReset.css';
import { UNSAFEWindowLocationHref } from '@webui-one/core/utils/unsafe';
import NSOLogo from './static/NSOLogo';
import style, { getFormStyle } from './LoginStyles';
import { isNonEmptyString } from '@webui-one/core/utils/string';

const REDIRECT_WAIT_SECONDS = 2;

const validateInput = (user, pwd) => (
    (!!user && user.length > 0) && (!!pwd && pwd.length > 0)
);

const getErrorMessage = (error) => {
    if (error.data && error.data.reason) {
        return error.data.reason;
    }
    return error.message;
};

const getInputStyle = (focusedInput, inputName) =>
    (focusedInput === inputName ? style.input.focused : style.input.unfocused);

const SSO = () => {
    const [decodeError, setDecodeError] = useState();
    const params = new URLSearchParams(window.location.search);
    const pkgs = params.get('pkgs');
    const nxt = params.get('next') || '/webui-one/index.html';

    if (!isNonEmptyString(pkgs)) return null;

    function parseProviderParamter(str) {
        const result = [];
        try {
            const pairs = str.split(';');

            for (const pair of pairs) {
                const [provider, url] = pair.split(':');
                result.push({
                    name: atob(provider),
                    url,
                });
            }
        } catch (e) {
            setDecodeError(getErrorMessage(e));
        }

        return result;
    }

    if (decodeError) {
        return (
            <div
                id="sso-error-message"
                style={decodeError ? style.errorTrue : style.errorFalse}
            >
                {decodeError}
            </div>
        );
    }

    return (
        <>
            {parseProviderParamter(pkgs).map(pkg => (
                <button
                    key={pkg.name}
                    id={pkg.name}
                    type="button"
                    style={style.button}
                    onClick={() => UNSAFEWindowLocationHref(`/sso${pkg.url}?next=${nxt}`)}
                >
                    {pkg.name}
                </button>
            ))}
        </>
    );
};


class Login extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
        this.handleChallenge = this.handleChallenge.bind(this);
        this.state = {
            challenge: '',
            error: undefined,
            focusedInput: undefined,
            isValid: false,
            password: '',
            pendingLogin: false,
            redirectSecs: -1,
            username: '',
        };
        this.timeout = null;
    }

    componentDidMount() {
        const { onCheckSession } = this.props;
        onCheckSession();
    }

    componentDidUpdate(prevProps, prevState) {
        const { isCheckingSession, redirectToWebui, sessionExists } = this.props;
        const { pendingLogin, redirectSecs } = this.state;
        const { sessionExists: prevExists } = prevProps;
        const { redirectSecs: prevSecs } = prevState;

        if (sessionExists && !prevExists) {
            this.updateRedirectTimer(REDIRECT_WAIT_SECONDS);
        }
        if (!isCheckingSession && pendingLogin) {
            this.login();
        }
        if (isPositiveNumber(redirectSecs) && prevSecs !== redirectSecs) {
            this.tickTimer(redirectSecs - 1);
        }
        if (redirectSecs === 0 && prevSecs === 1) {
            redirectToWebui();
        }
    }

    componentWillUnmount() {
        const { clearTimer } = this.props;
        clearTimer(this.timeout);
    }

    handleChallenge(event) {
        const { onChallenge, challengeId } = this.props;
        const { challenge } = this.state;
        onChallenge({ challengeId, response: challenge });
        this.setState({
            username: '',
            password: '',
            challenge: '',
        });
        event.preventDefault();
    }

    handleChange(event) {
        const { challengeId } = this.props;
        const { name, value } = event.target;
        const { error, onClearError } = this.props;
        const newState = { ...this.state, [name]: value };
        if (error && !challengeId) {
            onClearError();
        }
        const isValid = validateInput(newState.username, newState.password);
        this.setState({ ...newState, isValid });
    }

    handleFocus(inputName) {
        this.setState({ focusedInput: inputName });
    }

    handleSubmit(event) {
        const { isValid } = this.state;
        if (isValid) {
            this.maybeLogin();
        }
        event.preventDefault();
    }

    tickTimer(redirectSecs) {
        const { clearTimer, setTimer } = this.props;
        clearTimer(this.timeout);
        this.timeout = setTimer(() => {
            this.updateRedirectTimer(redirectSecs);
        }, 1000);
    }

    updateRedirectTimer(redirectSecs) {
        this.setState({ redirectSecs });
    }

    login() {
        const { onLogin } = this.props;
        const { password, username } = this.state;
        this.setState({ pendingLogin: false });
        onLogin(username, password);
    }

    maybeLogin() {
        this.setState({ pendingLogin: true });
    }

    renderChallengeForm() {
        const { challengePrompt, isLoading } = this.props;
        const { focusedInput, challenge } = this.state;

        return (
            <form onSubmit={this.handleChallenge} style={getFormStyle(isLoading)}>
                {!!challengePrompt && (
                    <p data-handle="challenge-prompt" style={style.prompt}>
                        {challengePrompt}
                    </p>
                )}
                <input
                    name="challenge"
                    autoComplete="challenge"
                    autoFocus
                    data-handle={'login-challenge'}
                    key="challenge"
                    onBlur={this.handleFocus}
                    onChange={this.handleChange}
                    onFocus={() => this.handleFocus('challenge')}
                    onKeyPress={this.handleKeyPress}
                    placeholder="challenge"
                    style={getInputStyle(focusedInput, 'challenge')}
                    type="text"
                    value={challenge}
                />
                <button style={style.button} type="submit">
                    Verify
                </button>
            </form>
        );
    }

    renderLoginForm() {
        const { isLoading } = this.props;

        const { isValid, focusedInput, username, password } = this.state;

        return (
            <form onSubmit={this.handleSubmit} style={getFormStyle(isLoading)}>
                <input
                    name="username"
                    autoComplete="username"
                    autoFocus
                    data-handle={'login-userInput'}
                    key="user"
                    onBlur={this.handleFocus}
                    onChange={this.handleChange}
                    onFocus={() => this.handleFocus('userInput')}
                    onKeyPress={this.handleKeyPress}
                    placeholder="Username"
                    style={getInputStyle(focusedInput, 'username')}
                    type="text"
                    value={username}
                />
                <input
                    name="password"
                    autoComplete="password"
                    data-handle={'login-pwdInput'}
                    key="password"
                    onBlur={this.handleFocus}
                    onChange={this.handleChange}
                    onFocus={() => this.handleFocus('pwdInput')}
                    onKeyPress={this.handleKeyPress}
                    placeholder="Password"
                    style={getInputStyle(focusedInput, 'pwdInput')}
                    type="password"
                    value={password}
                />
                <button
                    disabled={!isValid}
                    style={isValid ? style.button : style.buttonDisabled}
                    type="submit"
                >
                    Login
                </button>
            </form>
        );
    }

    renderRedirect() {
        const { redirectSecs } = this.state;
        const { username } = this.props;
        const foundMessage = `Found an existing session for user "${username}".`;
        const time = redirectSecs === 1 ? '1 second' : `${redirectSecs} seconds`;
        const redirectMessage = `Redirecting to webui in ${time}.`;
        return (
            <div
                data-handle="login-redirect-message"
                style={style.redirectStyle}
            >
                <div>{foundMessage}</div>
                <div>{redirectMessage}</div>
            </div>
        );
    }

    render() {
        const { challengeId, error, isLoading } = this.props;
        const { pendingLogin, redirectSecs } = this.state;

        const showRedirect = toNumber(redirectSecs) > -1;
        const showLoader = !showRedirect && (!!isLoading || pendingLogin);
        const showChallengeForm = !showRedirect && !!challengeId;
        const showLoginForm = !showRedirect && !challengeId;
        let errorMessage = '';
        if (error && !challengeId) {
            errorMessage = getErrorMessage(error);
        }
        return (
            <div style={style.application}>
                <div style={style.loginBox}>
                    <div style={style.logo}>
                        <NSOLogo />
                    </div>
                    <div style={style.headerText}>
                        Network Services Orchestrator
                    </div>
                    {showChallengeForm && this.renderChallengeForm()}
                    {showLoginForm && this.renderLoginForm()}
                    {showRedirect && this.renderRedirect()}
                    <SSO />
                    <div style={style.ciscoLogo} />
                    <div
                        id="login-error-message"
                        style={errorMessage ? style.errorTrue : style.errorFalse}
                    >
                        {errorMessage}
                    </div>
                    {showLoader && (
                        <div style={style.loader}>
                            <Loader color="grey" />
                        </div>
                    )}
                </div>
            </div>
        );
    }
}

Login.propTypes = {
    challengeId: PropTypes.string,
    challengePrompt: PropTypes.string,
    clearTimer: PropTypes.func.isRequired,
    error: PropTypes.shape(Object),
    isCheckingSession: PropTypes.bool,
    isLoading: PropTypes.bool,
    onChallenge: PropTypes.func.isRequired,
    onCheckSession: PropTypes.func.isRequired,
    onClearError: PropTypes.func.isRequired,
    onLogin: PropTypes.func.isRequired,
    redirectToWebui: PropTypes.func.isRequired,
    sessionExists: PropTypes.bool,
    setTimer: PropTypes.func.isRequired,
    username: PropTypes.string,
};

Login.defaultProps = {
    clearTimer: clearTimeout,
    setTimer: setTimeout,
};

export default Login;

export {
    getErrorMessage as _getErrorMessage,
    REDIRECT_WAIT_SECONDS as _REDIRECT_WAIT_SECONDS,
    validateInput as _validateInput,
    SSO as _SSO,
};
